import { fetchRecordingAudio, transcribeWhisper } from '../core/audio';
import { getCallStatus, getTenantName } from '../core/db';
import { sendTextbeltSms } from '../core/sms';

type ProcessCallParams = {
  callSid: string;
  fromPhone: string;
  toPhone?: string;
  recordingUrl: string;
};

type ProcessCallDeps = {
  db: D1Database;
  openaiKey: string;
  textbeltKey: string;
  ownerPhone: string;
  twilioSid: string;
  twilioToken: string;
};

export async function processCall(params: ProcessCallParams, deps: ProcessCallDeps): Promise<void> {
  const { callSid, fromPhone, toPhone, recordingUrl } = params;
  const { db, openaiKey, textbeltKey, ownerPhone, twilioSid, twilioToken } = deps;

  const tenantName = (await getTenantName(db, toPhone)) || 'the office';

  try {
    const status = await getCallStatus(db, callSid);
    if (status === 'completed') {
      console.log('Phase 2 skipped (already completed)', { callSid });
      return;
    }

    await db.prepare(
      "UPDATE calls SET status = ?, recording_url = ? WHERE provider_call_id = ?"
    ).bind('recorded', recordingUrl, callSid).run();

    console.log('PHASE 2 START:', { recordingUrl });

    let transcript = 'Voice message received';
    const audioResult = await fetchRecordingAudio(recordingUrl, twilioSid, twilioToken);
    if (audioResult.blob && audioResult.status === 200) {
      try {
        transcript = await transcribeWhisper(openaiKey, audioResult.blob);
      } catch (e) {
        console.error('Whisper transcription failed:', e);
        transcript = 'Voice message received';
      }
    } else {
      console.warn('Audio unavailable; continuing without Whisper.');
    }

    try {
      await db.prepare(
        "UPDATE calls SET transcription = ? WHERE provider_call_id = ?"
      ).bind(transcript, callSid).run();
      console.log('TRANSCRIPTION SUCCESS:', transcript);
    } catch (e) {
      console.error('D1 transcription update failed:', e);
    }

    const leadMessage = `Hi, this is the team at ${tenantName || "the office"}.`;
    await sendTextbeltSms(textbeltKey, fromPhone, leadMessage);
    console.log('SUCCESS: SMS SENT');

    const ownerMessage = `Hi, this is the team at ${tenantName || "the office"}.`;
    await sendTextbeltSms(textbeltKey, ownerPhone, ownerMessage);
    console.log('SUCCESS: SMS SENT');

    await db.prepare(
      "UPDATE calls SET status = ? WHERE provider_call_id = ?"
    ).bind('completed', callSid).run();
  } catch (error) {
    console.error('Phase 2 Error:', error);
  }
}
