export async function sendTextbeltSms(apiKey: string | undefined, phone: string, message: string): Promise<void> {
  if (!apiKey || apiKey === 'undefined') {
    console.error('CRITICAL: TEXTBELT_API_KEY is not set in Wrangler secrets.');
    return;
  }

  try {
    const response = await fetch('https://textbelt.com/text', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ phone, message, key: apiKey }),
    });

    const result: any = await response.json();
    if (!result.success) {
      throw new Error(`Textbelt Error: ${result.error || 'Unknown error'}`);
    }
  } catch (e) {
    console.error('Textbelt send failed:', e);
  }
}
