import { Context } from 'hono';

type Bindings = {
  SYSTEMIX_DB: D1Database;
  OPENAI_API_KEY: string;
  TEXTBELT_API_KEY: string;
  CLIENT_PHONE: string;
  TWILIO_ACCOUNT_SID: string;
  TWILIO_AUTH_TOKEN: string;
};

// ==========================================
// FIX #1: PERSONALIZED /voice ENDPOINT
// Looks up company name for personalized greeting
// ==========================================
export async function twilioVoiceHandler(c: Context<{ Bindings: Bindings }>) {
  try {
    const env = c.env;
    const formData = await c.req.formData();
    const from = formData.get('From') as string;
    const to = formData.get('To') as string;
    const callSid = formData.get('CallSid') as string;

    console.log('📞 Incoming call:', { from, to, callSid });

    // ==========================================
    // LOOKUP COMPANY NAME FOR PERSONALIZED GREETING
    // ==========================================
    let companyName = 'us'; // Fallback if no tenant found
    try {
      const tenantQuery = await env.SYSTEMIX_DB.prepare(
        'SELECT company_name FROM tenants WHERE phone_number = ?'
      ).bind(to).first();
      
      if (tenantQuery?.company_name) {
        companyName = tenantQuery.company_name as string;
        console.log('✅ Personalized greeting for:', companyName);
      } else {
        console.log('⚠️ No tenant found for', to, '- using generic greeting');
      }
    } catch (dbError) {
      console.error('❌ Tenant lookup failed:', dbError);
      // Continue with fallback greeting
    }

    // Get base URL for the callback
    const baseUrl = new URL(c.req.url).origin;

    // Personalized TwiML with company name
    const twiml = `<?xml version="1.0" encoding="UTF-8"?>
<Response>
  <Say voice="Polly.Joanna-Neural" language="en-US">
    <prosody rate="95%" pitch="-5%">
      Thank you for calling ${companyName}. We're unable to take your call right now, but we'd love to hear from you. Please leave a detailed message after the tone, and we'll get back to you as soon as possible.
    </prosody>
  </Say>
  <Record 
    maxLength="120" 
    timeout="10" 
    playBeep="true" 
    transcribe="false"
    recordingStatusCallback="${baseUrl}/v1/webhooks/twilio/recording"
  />
</Response>`;

    return c.text(twiml, 200, { 'Content-Type': 'text/xml' });

  } catch (error) {
    console.error('❌ Voice handler error:', error);
    
    // Return valid TwiML even on error to prevent Twilio retries
    const errorTwiml = `<?xml version="1.0" encoding="UTF-8"?>
<Response>
  <Say>Sorry, we're experiencing technical difficulties. Please try again later.</Say>
  <Hangup/>
</Response>`;
    
    return c.text(errorTwiml, 200, { 'Content-Type': 'text/xml' });
  }
}

// ==========================================
// NEW: /recording CALLBACK ENDPOINT
// ALL processing happens here AFTER call ends
// ==========================================
export async function twilioRecordingHandler(c: Context<{ Bindings: Bindings }>) {
  try {
    const env = c.env;
    const formData = await c.req.formData();
    
    const recordingUrl = formData.get('RecordingUrl') as string;
    const recordingSid = formData.get('RecordingSid') as string;
    const callSid = formData.get('CallSid') as string;
    const from = formData.get('From') as string;
    const to = formData.get('To') as string;
    const recordingDuration = formData.get('RecordingDuration') as string;

    console.log('🎙️ Processing recording:', { 
      recordingSid, 
      callSid, 
      from, 
      to,
      duration: recordingDuration 
    });

    // ==========================================
    // FIX #3: TENANT BRANDING LOOKUP
    // ==========================================
    let companyName = 'the office';
    try {
      const tenantQuery = await env.SYSTEMIX_DB.prepare(
        'SELECT company_name FROM tenants WHERE phone_number = ?'
      ).bind(to).first();
      
      if (tenantQuery?.company_name) {
        companyName = tenantQuery.company_name as string;
        console.log('✅ Found tenant:', companyName);
      } else {
        console.log('⚠️ No tenant found for', to, '- using default branding');
      }
    } catch (dbError) {
      console.error('❌ Tenant lookup failed:', dbError);
    }

    // ==========================================
    // FETCH AUDIO FROM TWILIO
    // ==========================================
    let audioBlob: Blob | null = null;
    try {
      const audioUrl = `${recordingUrl}.wav`;
      const authHeader = 'Basic ' + btoa(`${env.TWILIO_ACCOUNT_SID}:${env.TWILIO_AUTH_TOKEN}`);
      
      console.log('⬇️ Fetching audio from:', audioUrl);
      
      const audioResponse = await fetch(audioUrl, {
        headers: { 
          'Authorization': authHeader 
        },
        // Add timeout to prevent hanging
        signal: AbortSignal.timeout(10000) // 10 second timeout
      });

      if (audioResponse.ok) {
        audioBlob = await audioResponse.blob();
        console.log('✅ Audio fetched, size:', audioBlob.size, 'bytes');
      } else {
        console.error('❌ Audio fetch failed:', audioResponse.status, await audioResponse.text());
      }
    } catch (audioError) {
      console.error('❌ Audio fetch error:', audioError);
    }

    // ==========================================
    // FIX #2: OPENAI WHISPER TRANSCRIPTION
    // Fixed Authorization header format
    // ==========================================
    let transcript = 'Voice message received';
    
    if (audioBlob && audioBlob.size > 0) {
      try {
        const formData = new FormData();
        formData.append('file', audioBlob, 'recording.wav');
        formData.append('model', 'whisper-1');
        formData.append('language', 'en');

        console.log('🎯 Calling OpenAI Whisper...');
        console.log('   API Key present:', env.OPENAI_API_KEY ? '✅' : '❌ MISSING');

        const whisperResponse = await fetch('https://api.openai.com/v1/audio/transcriptions', {
          method: 'POST',
          headers: {
            // CRITICAL FIX: Proper Bearer token format
            'Authorization': `Bearer ${env.OPENAI_API_KEY}`
          },
          body: formData
        });

        if (whisperResponse.ok) {
          const result = await whisperResponse.json() as { text: string };
          transcript = result.text.trim();
          console.log('✅ Transcription successful:', transcript.substring(0, 100) + '...');
        } else {
          const errorText = await whisperResponse.text();
          console.error('❌ Whisper API failed:', whisperResponse.status, errorText);
          
          // If it's an auth error, the API key is wrong
          if (whisperResponse.status === 401) {
            console.error('🔑 OpenAI API Key is invalid or not set!');
          }
        }
      } catch (whisperError) {
        console.error('❌ Whisper transcription error:', whisperError);
      }
    } else {
      console.log('⚠️ No audio blob available, skipping transcription');
    }

    // ==========================================
    // SAVE TO DATABASE
    // ==========================================
    try {
      await env.SYSTEMIX_DB.prepare(`
        INSERT INTO calls (
          call_sid, 
          from_number, 
          to_number, 
          recording_url, 
          transcript, 
          created_at
        ) VALUES (?, ?, ?, ?, ?, datetime('now'))
      `).bind(callSid, from, to, recordingUrl, transcript).run();
      
      console.log('✅ Call saved to database');
    } catch (dbError) {
      console.error('❌ Database save failed:', dbError);
      // Continue to send SMS even if DB fails
    }

    // ==========================================
    // SEND SMS NOTIFICATION
    // ==========================================
    try {
      const smsMessage = `New voicemail at ${companyName} from ${from}:\n\n"${transcript}"`;
      
      // Clean phone number for Textbelt
      // Remove + symbol and whitespace
      let cleanPhone = env.CLIENT_PHONE;
      if (cleanPhone.startsWith('+')) {
        cleanPhone = cleanPhone.substring(1);
      }
      cleanPhone = cleanPhone.replace(/\s/g, '');
      
      console.log('📱 Sending SMS to:', cleanPhone);
      console.log('   Message preview:', smsMessage.substring(0, 50) + '...');
      
      const smsResponse = await fetch('https://textbelt.com/text', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          phone: cleanPhone,
          message: smsMessage,
          key: env.TEXTBELT_API_KEY
        })
      });

      const smsResult = await smsResponse.json() as any;
      
      if (smsResult.success) {
        console.log('✅ SMS sent successfully');
      } else {
        console.error('❌ SMS send failed:', smsResult);
      }
    } catch (smsError) {
      console.error('❌ SMS error:', smsError);
    }

    // Return 200 OK to Twilio (prevents retries)
    return c.json({ success: true, transcript }, 200);

  } catch (error) {
    console.error('❌ Recording handler error:', error);
    // IMPORTANT: Still return 200 to prevent Twilio from retrying
    return c.json({ success: false, error: String(error) }, 200);
  }
}